import React from 'react'
import { observer, inject } from 'mobx-react'
import { Descriptions } from 'antd'
import CustomUpload from '@/components/customAnt/upload'
import urls from "@/http/urls"

interface Props{
    UserStore: any
}

function AdminIndex({UserStore}: Props){
    const { userInfo } = UserStore

    const onAvatarChange = () => {
        UserStore.updateUser()
    }

    return (
        <>
            <Descriptions title="" bordered>
                <Descriptions.Item label="">{ userInfo.name }</Descriptions.Item>
                <Descriptions.Item label="">{ userInfo.mobile }</Descriptions.Item>
                <Descriptions.Item label="">{ userInfo.password }</Descriptions.Item>
                <Descriptions.Item label="">{ userInfo.create_time }</Descriptions.Item>
                <Descriptions.Item label="">{ userInfo.modify_time }</Descriptions.Item>
                <Descriptions.Item label="">
                    <CustomUpload value={userInfo.avatar} onChange={onAvatarChange} uploadPath={urls.adminUpdateAvatar}/>
                </Descriptions.Item>
            </Descriptions>
        </>
    )
}

export default inject('UserStore')(observer(AdminIndex))
